.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #070d44;
}

.App ul li {
  list-style: none;
  display: inline;
  padding-left: .5rem;
}
.App ul li::before {
  /* content: "/"; */
  color: #070d44;
}

.App button, .App select {
  cursor: pointer;
}

.App button:hover {
  background: #f7f8f9;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
